.navbar-brand {
    color: rgb(250, 250, 250) !important;
}

.logo {
    height: 1.4em !important;
    width: 2.5em !important;
}

.navbar-nav .nav-link {
    color: white !important;
    padding-right: 1rem;
    padding-left: 1rem;
    font-size: x-small;
}

.nav-link {
    padding: 0.8rem 1rem !important;

}

.navbar-nav .nav-item {
    position: relative;
    margin-left: 20px;
    text-align: right;

}

.navbar-nav .nav-item a {
    font-weight: 400;
    transition: all 0.3s ease-out 0s;
    position: relative;
    z-index: 1;
}

.navbar-nav .nav-item:nth-child(-n+4) a::after {
    content: "";
    position: relative;
    display: block;
    height: 5px;
    width: 0;
    border-radius: 16px;
    background: #c95bf5;
    bottom: 1px;
    left: 0;
    z-index: -1;
    transition: all 0.3s ease-out 0s;
}

.navbar-nav .nav-item a:hover::after {
    width: 100%;
}

.fork-btn {
    font-size: 1.1em;
