.navbar {

    background-color: #171717 !important;
}

.brand {
    margin-top: 10px;
}
a{
    color: white;
    font-size: small;
}

@media (max-width: 806px) {
    .navbar {
        padding: 0rem 2rem 0.5rem 2rem !important;
        font-size: 1.4rem !important;
        background-color: #171717 !important;
    }

    .navbar-nav .nav-item a::after {
        display: none !important;
    }

    .brand {
        margin-top: 10px;
        color: #d985fa;
    }

    .nav-link {
        padding: 0.7rem 1rem !important;
    }
}

.navbar-brand {
    color: rgb(250, 250, 250) !important;
}

.logo {
    height: 1.4em !important;
    width: 2.5em !important;
}

.navbar-nav .nav-link {
    color: white !important;
    padding-right: 1rem;
    padding-left: 1rem;
    font-size: x-small;
}

.nav-link {
    padding: 0.8rem 1rem !important;

}

.navbar-nav .nav-item {
    position: relative;
    margin-left: 20px;
    text-align: right;

}

.navbar-nav .nav-item a {
    font-weight: 400;
    transition: all 0.3s ease-out 0s;
    position: relative;
    z-index: 1;
}

.navbar-nav .nav-item:nth-child(-n+4) a::after {
    content: "";
    position: relative;
    display: block;
    height: 5px;
    width: 0;
    border-radius: 16px;
    background: #c95bf5;
    bottom: 1px;
    left: 0;
    z-index: -1;
    transition: all 0.3s ease-out 0s;
}

.navbar-nav .nav-item a:hover::after {
    width: 100%;
}

.fork-btn {
    font-size: 1.1em;
    padding-top: 10px;
}

.fork-btn-inner {
    line-height: 1.4em;
    background-color: #934cce5e;
    padding: 0.25rem 1.1rem;
    vertical-align: middle;
    text-align: center;
}

.fork-btn-inner:hover {
    transform: translateY(-2px);
    background-color: #a24dd386;
    border-color: #a24dd386;
}

.fork-btn-inner::after {
    display: none;
}
.notification-icon .MuiSvgIcon-root{
    color: white;
    float: right;
}
a.Dashboard-heading{
    font-size: medium !important;
    text-align: left;
}

